import tilt from "vanilla-tilt";

tilt.init(document.querySelectorAll(".imagenes"), {
  scale: "1.05",
  glare: true,
  maxGlare: "0.3"
});
function moveToSelected(element) {
  if (element === "next") {
    var selected = $(".selected").next();
  } else if (element === "prev") {
    selected = $(".selected").prev();
  } else {
    selected = element;
  }

  var next = $(selected).next();
  var prev = $(selected).prev();
  var prevSecond = $(prev).prev();
  var nextSecond = $(next).next();

  $(selected)
    .removeClass()
    .addClass("selected");

  $(prev)
    .removeClass()
    .addClass("prev");
  $(next)
    .removeClass()
    .addClass("next");

  $(nextSecond)
    .removeClass()
    .addClass("nextRightSecond");
  $(prevSecond)
    .removeClass()
    .addClass("prevLeftSecond");

  $(nextSecond)
    .nextAll()
    .removeClass()
    .addClass("hideRight");
  $(prevSecond)
    .prevAll()
    .removeClass()
    .addClass("hideLeft");
}

// Eventos teclado
document.addEventListener("keydown", function(e) {
  switch (e.which) {
    case 37: // left
      moveToSelected("prev");
      break;

    case 39: // right
      moveToSelected("next");
      break;

    default:
      return;
  }
  e.preventDefault();
});
let carouseldiv = document.querySelector("#carousel div");
carouseldiv.onclick = function() {
  moveToSelected($(this));
};
let prev = document.querySelector("#prev");
prev.onclick = function() {
  moveToSelected("prev");
};
let next = document.querySelector("#next");
next.onclick = function() {
  moveToSelected("next");
};
